.dropdown__btn {
  position: relative;
  padding: 0 35px 0 15px; }
  .dropdown__btn::after {
    content: "";
    position: absolute;
    right: 10px;
    top: calc(50%);
    background-image: url(../images//arrow.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 19px;
    height: 19px;
    transform: translateY(-50%); }

.dropdown__content {
  transition: 0.3s all;
  opacity: 0;
  background-color: #5c42aa;
  position: absolute;
  max-width: 175px;
  width: 100%; }
  .dropdown__content-link {
    display: block;
    padding: 10px 15px;
    font-size: 14px;
    line-height: 20px;
    color: #fff;
    font-weight: 700;
    position: relative;
    z-index: 1; }
    .dropdown__content-link:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      background-color: transparent;
      width: 100%;
      height: 100%;
      z-index: -1; }
    .dropdown__content-link:hover::before {
      background-color: rgba(255, 255, 255, 0.1); }

.menu {
  transform: translateX(-100%);
  transition: transform 0.5s cubic-bezier(0.79, 0.14, 0.15, 0.86);
  position: fixed;
  z-index: 10000000;
  background-color: rgba(49, 59, 69, 0.98);
  max-width: 100vw;
  width: 100%;
  min-height: 100vh;
  height: auto;
  padding-left: 20px;
  padding-right: 20px; }
  .menu.active {
    transform: translateX(0); }
  .menu__socials {
    margin-top: 40px;
    display: flex;
    gap: 0 20px; }
  .menu__socials-item:nth-child(1) {
    margin-top: 5px; }
    .menu__socials-item:nth-child(1) svg {
      width: 22px;
      height: 22px; }
  .menu__socials-item:nth-child(2) {
    margin-top: 5px; }
    .menu__socials-item:nth-child(2) svg {
      width: 22px;
      height: 22px; }
      .menu__socials-item:nth-child(2) svg path {
        stroke: rgba(49, 59, 69, 0.98); }
        .menu__socials-item:nth-child(2) svg path:nth-child(1) {
          stroke: none; }
  .menu__socials-item:nth-child(3) {
    margin-top: 4px; }
    .menu__socials-item:nth-child(3) svg {
      width: 23px;
      height: 23px; }
  .menu__socials-item:nth-child(4) {
    margin-top: 3px; }
    .menu__socials-item:nth-child(4) svg {
      width: 26px;
      height: 26px; }
  .menu__socials-item:nth-child(5) svg {
    width: 30px;
    height: 30px; }
  .menu__socials-link {
    display: inline-block;
    height: 50px; }
    .menu__socials-link svg {
      width: 100px;
      height: 100px; }
      .menu__socials-link svg path {
        fill: #fff; }
      .menu__socials-link svg rect {
        stroke: transparent; }
  .menu__list-item button,
  .menu__list-item a {
    display: block !important; }
  .menu__list-item button {
    position: relative;
    width: 100%;
    text-align: left; }
  .menu__list-item:nth-child(2):hover ul, .menu__list-item:nth-child(5):hover ul {
    max-height: 180px; }
  .menu__list-item:nth-child(2):hover button::after, .menu__list-item:nth-child(5):hover button::after {
    transform: translateY(-50%) rotate(0deg); }
  .menu__list-item:nth-child(2) button::after, .menu__list-item:nth-child(5) button::after {
    transition: 0.3s all;
    content: "";
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%) rotate(-90deg);
    background-image: url(../images/arrow.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 30px;
    height: 30px; }
  .menu__list-link, .menu__dropdown-link {
    display: inline-block;
    line-height: 21px;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
    margin-left: 0;
    padding: 15px 30px 15px 0; }
  .menu__dropdown-link {
    padding: 9px 30px;
    line-height: normal; }
  .menu__dropdown {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.4s ease-out; }
  .menu__close {
    display: inline-block;
    position: absolute;
    right: 5%;
    top: 20px;
    width: 70px;
    height: 70px; }
    .menu__close span {
      position: absolute;
      width: 40px;
      height: 2px;
      background-color: #fff;
      left: 50%;
      top: 50%; }
      .menu__close span:first-child {
        transform: translate(-50%, -50%) rotate(45deg); }
      .menu__close span:last-child {
        transform: translate(-50%, -50%) rotate(-45deg); }

